<template>
  <div class="change-bank-phone">
    <div>
      <el-steps :active="active" finish-status="success" align-center>
        <el-step title="填写手机号"></el-step>
        <el-step title="变更手机号"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <el-row class="list">
        <el-col :span="24">
          <div>
            存管账户：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input1" placeholder="请输入内容"></el-input>
          </div>
          <div>
            账户类型：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input2" placeholder="请输入内容"></el-input>
          </div>
          <div>
            现有手机号：<el-input :disabled="true" style="width: 30%" size="medium" v-model="input3" placeholder="请输入内容"></el-input>
          </div>
          <div>
            新的手机号：<el-input :disabled="active === 2" style="width: 30%" size="medium" v-model="input4" placeholder="请输入手机号"></el-input>
          </div>
        </el-col>
      </el-row>
      <el-button type="primary" style="margin-top: 30px;" @click="nowChangePhone"  v-if="active === 2">立即变更</el-button>
      <el-button style="margin-top: 30px;" @click="next">{{nextText}}</el-button>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Axios from 'axios'
import APIURL from '@/config/APIURL'
Axios.defaults.timeout = 5000
Axios.defaults.withCredentials = true
Axios.defaults.headers.post['Content-Type'] = 'application/json; charset=utf-8'
Vue.prototype.$ajax = Axios
export default {
  name: 'change-bank-phone',
  data () {
    return {
      active: 1,
      nextText: '下一步',
      input1: '',
      input2: '',
      input3: '',
      input4: '',
      reqParam: {
        'appId': 'org_crm_merchant',
        'loginPhone': sessionStorage.getItem('phone'),
        'reqTime': this.getNowFormatDate(),
        'token': sessionStorage.getItem('token'),
        'recoCode': sessionStorage.getItem('recoCode'),
        'legalPhoneNew': ''
      }
    }
  },
  created () {
    this.AcctInfo = JSON.parse(sessionStorage.getItem('AcctInfo'))
    this.input1 = this.AcctInfo.depositAcct
    this.input2 = this.AcctInfo.businessTypeMsg
    this.input3 = this.AcctInfo.realLegalPhone
  },
  methods: {
    next () {
      // if (this.input4) {
      this.active++
      // } else {
      //   alert('请输入新手机号')
      // }
      if (this.active === 2) {
        this.nextText = '上一步'
      } else if (this.active > 2) {
        this.active = 1
        this.nextText = '下一步'
      }
    },
    nowChangePhone () {
      this.$ajax.post(APIURL.modifyMerchantPhone, this.reqParam).then((res) => {
        if (res.data.code === 0) {
          location.href = res.data.data.jumpUrl
        } else {
          alert(res.data.msg)
        }
      })
    },
    getNowFormatDate () {
      let date = new Date()
      let seperator1 = '-'
      let seperator2 = ':'
      let month = date.getMonth() + 1
      let strDate = date.getDate()
      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      let currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate + ' ' + date.getHours() + seperator2 + date.getMinutes() + seperator2 + date.getSeconds()
      return currentdate
    }
  },
  watch: {
    input4: function (newValue, oldValue) {
      this.reqParam.legalPhoneNew = newValue
    }
  },
  computed: {
  },
  components: {
  }
}
</script>
<style lang="scss" scoped>
  .change-bank-phone{
    div{
      line-height: 1.2;
      text-align: right;
    }
    .el-steps{
      padding: 20px 0;
    }
    .list{
      div{
        width: 80%;
        margin-top: 5px;
      }
    }
    .el-table{
      margin-top: 30px;
    }
    .el-button{
      display: block;
      margin: 0 auto;
    }
  }
</style>



